/*
 * Data Table
 */
/*
 * Sort
 */
[md2SortBy] {
  line-height: 24px;
  color: rgba(0, 0, 0, 0.54);
  white-space: nowrap;
  cursor: pointer;
  user-select: none; }
  [md2SortBy] svg {
    display: inline-block;
    vertical-align: middle;
    fill: currentColor;
    opacity: 0; }
  [md2SortBy]:hover:not(.md2-sort-active) svg {
    color: rgba(0, 0, 0, 0.26);
    opacity: 1; }
  [md2SortBy].md2-sort-active {
    color: rgba(0, 0, 0, 0.87); }
    [md2SortBy].md2-sort-active svg {
      opacity: 1; }

/*
 * Pagination
 */
md2-pagination {
  display: block;
  color: rgba(0, 0, 0, 0.54);
  user-select: none; }
  md2-pagination::before, md2-pagination::after {
    display: table;
    content: ''; }
  md2-pagination::after {
    clear: both; }
  md2-pagination .md2-pagination {
    display: inline-block;
    margin: 8px 0;
    padding: 0; }
    md2-pagination .md2-pagination li {
      position: relative;
      display: inline-block;
      width: 36px;
      vertical-align: top;
      text-align: center;
      line-height: 36px;
      border-radius: 100px;
      cursor: pointer;
      box-sizing: border-box; }
      md2-pagination .md2-pagination li:hover:not(.disabled):not(.active) {
        background: rgba(0, 0, 0, 0.12); }
      md2-pagination .md2-pagination li.disabled {
        pointer-events: none;
        background: transparent;
        cursor: default;
        opacity: 0.48; }
      md2-pagination .md2-pagination li.active {
        background: #106cc8;
        color: white;
        cursor: default; }
      md2-pagination .md2-pagination li svg {
        fill: currentColor;
        margin-bottom: -7px; }
  md2-pagination .md2-rows-select {
    display: inline-block;
    margin: 8px 0;
    padding: 0;
    float: right;
    color: rgba(0, 0, 0, 0.54);
    line-height: 36px; }
    md2-pagination .md2-rows-select md2-select {
      display: inline-block;
      border: 0;
      outline: 0; }
    md2-pagination .md2-rows-select .md2-select-trigger {
      border-width: 0;
      min-width: 40px; }
